/* thread */
.aui-thread-root {
    box-sizing: border-box;
    height: 100%;
    background-color: hsl(var(--aui-background))
}
.aui-thread-root>.aui-thread-viewport {
    background-color: inherit
}

.aui-thread-viewport {
    display: flex;
    height: 100%;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
    scroll-behavior: smooth;
    background-color: hsl(var(--aui-background));
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 2rem
}

.aui-thread-viewport-footer {
    position: sticky;
    bottom: 0px;
    margin-top: 0.75rem;
    display: flex;
    width: 100%;
    max-width: var(--aui-thread-max-width);
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    border-top-left-radius: var(--aui-radius);
    border-top-right-radius: var(--aui-radius);
    background-color: inherit;
    padding-bottom: 1rem
}

.aui-thread-scroll-to-bottom {
    position: absolute;
    top: -2rem;
    border-radius: 9999px
}

.aui-thread-scroll-to-bottom:disabled {
    visibility: hidden
}

.aui-thread-followup-suggestions {
    display: flex;
    min-height: 2rem;
    align-items: center;
    justify-content: center;
    gap: 0.5rem
}

.aui-thread-followup-suggestion {
    border-radius: 9999px;
    border-width: 1px;
    background-color: hsl(var(--aui-background));
    padding-left: 0.75rem;
    padding-right: 0.75rem;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    font-size: 0.875rem;
    line-height: 1.25rem;
    transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    transition-timing-function: cubic-bezier(0.4, 0, 1, 1);
    animation-timing-function: cubic-bezier(0.4, 0, 1, 1)
}

.aui-thread-followup-suggestion:hover {
    background-color: hsl(var(--aui-muted) / 0.8)
}

/* thread welcome */

.aui-thread-welcome-root {
    display: flex;
    width: 100%;
    max-width: var(--aui-thread-max-width);
    flex-grow: 1;
    flex-direction: column
}

.aui-thread-welcome-center {
    display: flex;
    width: 100%;
    flex-grow: 1;
    flex-direction: column;
    align-items: center;
    justify-content: center
}

.aui-thread-welcome-message {
    margin-top: 1rem;
    font-weight: 500
}

.aui-thread-welcome-suggestion-container {
    margin-top: 0.75rem;
    display: flex;
    width: 100%;
    align-items: stretch;
    justify-content: center;
    gap: 1rem
}

.aui-thread-welcome-suggestion {
    display: flex;
    max-width: 24rem;
    flex-grow: 1;
    flex-basis: 0px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: var(--aui-radius);
    border-width: 1px;
    padding: 0.75rem;
    transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    transition-timing-function: cubic-bezier(0.4, 0, 1, 1);
    animation-timing-function: cubic-bezier(0.4, 0, 1, 1)
}

.aui-thread-welcome-suggestion:hover {
    background-color: hsl(var(--aui-muted) / 0.8)
}

.aui-thread-welcome-suggestion-text {
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    text-overflow: ellipsis;
    font-size: 0.875rem;
    line-height: 1.25rem;
    font-weight: 600
}

/* thread composer */

.aui-composer-root {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    align-items: flex-end;
    border-radius: var(--aui-radius);
    border-width: 1px;
    background-color: inherit;
    padding-left: 0.625rem;
    padding-right: 0.625rem;
    --aui-shadow: 0 1px 2px 0 rgb(0 0 0 / 0.05);
    --aui-shadow-colored: 0 1px 2px 0 var(--aui-shadow-color);
    box-shadow: var(--aui-ring-offset-shadow, 0 0 #0000), var(--aui-ring-shadow, 0 0 #0000), var(--aui-shadow);
    transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    transition-timing-function: cubic-bezier(0.4, 0, 1, 1);
    animation-timing-function: cubic-bezier(0.4, 0, 1, 1)
}

.aui-composer-root:focus-within {
    border-color: hsl(var(--aui-ring) / 0.2)
}

.aui-composer-input {
    max-height: 10rem;
    flex-grow: 1;
    resize: none;
    border-style: none;
    background-color: transparent;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-size: 0.875rem;
    line-height: 1.25rem;
    outline: 2px solid transparent;
    outline-offset: 2px
}

.aui-composer-input::-moz-placeholder {
    color: hsl(var(--aui-muted-foreground))
}

.aui-composer-input::placeholder {
    color: hsl(var(--aui-muted-foreground))
}

.aui-composer-input:focus {
    --aui-ring-offset-shadow: var(--aui-ring-inset) 0 0 0 var(--aui-ring-offset-width) var(--aui-ring-offset-color);
    --aui-ring-shadow: var(--aui-ring-inset) 0 0 0 calc(0px + var(--aui-ring-offset-width)) var(--aui-ring-color);
    box-shadow: var(--aui-ring-offset-shadow), var(--aui-ring-shadow), var(--aui-shadow, 0 0 #0000)
}

.aui-composer-input:disabled {
    cursor: not-allowed
}

.aui-composer-send,
.aui-composer-cancel,
.aui-composer-attach {
    margin-top: 0.625rem;
    margin-bottom: 0.625rem;
    width: 2rem;
    height: 2rem;
    padding: 0.5rem;
    transition-property: opacity;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    transition-timing-function: cubic-bezier(0.4, 0, 1, 1);
    animation-timing-function: cubic-bezier(0.4, 0, 1, 1)
}

.aui-composer-attachments {
    display: flex;
    width: 100%;
    flex-direction: row;
    gap: 0.75rem;
    padding-left: 2.5rem;
    padding-right: 2.5rem
}

/* attachment */

.aui-attachment-root {
    position: relative;
    margin-top: 0.75rem
}

.aui-attachment-content {
    display: flex;
    height: 3rem;
    width: 10rem;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    border-radius: var(--aui-radius);
    border-width: 1px;
    padding: 0.25rem
}

.aui-attachment-preview-trigger {
    cursor: pointer;
    transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms
}

.aui-attachment-preview-trigger:hover {
    background-color: hsl(var(--aui-accent) / 0.5)
}

.aui-attachment-thumb {
    display: flex;
    width: 2.5rem;
    height: 2.5rem;
    align-items: center;
    justify-content: center;
    border-radius: 0.25rem;
    border-width: 1px;
    background-color: hsl(var(--aui-muted));
    font-size: 0.875rem;
    line-height: 1.25rem
}

.aui-attachment-text {
    flex-grow: 1;
    flex-basis: 0px
}

.aui-attachment-name {
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    text-overflow: ellipsis;
    word-break: break-all;
    font-size: 0.75rem;
    line-height: 1rem;
    font-weight: 700;
    color: hsl(var(--aui-muted-foreground))
}

.aui-attachment-type {
    font-size: 0.75rem;
    line-height: 1rem;
    color: hsl(var(--aui-muted-foreground))
}

.aui-attachment-remove {
    position: absolute;
    right: -0.75rem;
    top: -0.75rem;
    width: 1.5rem;
    height: 1.5rem;
    color: hsl(var(--aui-muted-foreground))
}

.aui-attachment-remove>svg {
    width: 1rem;
    height: 1rem;
    border-radius: 9999px;
    background-color: hsl(var(--aui-background))
}

/* user message */

.aui-user-message-root {
    display: grid;
    grid-auto-rows: auto;
    grid-template-columns: minmax(72px,1fr) auto;
    row-gap: 0.5rem
}

.aui-user-message-root>* {
    grid-column-start: 2
}

.aui-user-message-root {
    width: 100%;
    max-width: var(--aui-thread-max-width);
    padding-top: 1rem;
    padding-bottom: 1rem
}

:where(.aui-user-message-root) > .aui-user-action-bar-root {
    grid-column-start: 1;
    grid-row-start: 2;
    margin-right: 0.75rem;
    margin-top: 0.625rem
}

:where(.aui-user-message-root) > .aui-user-message-attachments {
    grid-column: 1 / -1;
    grid-column-start: 1;
    grid-row-start: 1;
    justify-content: flex-end
}

:where(.aui-user-message-root) > .aui-user-message-content {
    grid-column-start: 2;
    grid-row-start: 2
}

:where(.aui-user-message-root) > .aui-branch-picker-root {
    grid-column: 1 / -1;
    grid-column-start: 1;
    grid-row-start: 3;
    margin-right: -0.25rem;
    justify-content: flex-end
}

.aui-user-message-content {
    max-width: calc(var(--aui-thread-max-width) * 0.8);
    overflow-wrap: break-word;
    border-radius: 1.5rem;
    background-color: hsl(var(--aui-muted));
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    padding-top: 0.625rem;
    padding-bottom: 0.625rem;
    color: hsl(var(--aui-foreground))
}

.aui-user-message-attachments {
    display: flex;
    width: 100%;
    flex-direction: row;
    gap: 0.75rem
}

/* user action bar */

.aui-user-action-bar-root {
    display: flex;
    flex-direction: column;
    align-items: flex-end
}

/* edit composer */

.aui-edit-composer-root {
    margin-top: 1rem;
    margin-bottom: 1rem;
    display: flex;
    width: 100%;
    max-width: var(--aui-thread-max-width);
    flex-direction: column;
    gap: 0.5rem;
    border-radius: 0.75rem;
    background-color: hsl(var(--aui-muted))
}

.aui-edit-composer-input {
    display: flex;
    height: 2rem;
    width: 100%;
    resize: none;
    background-color: transparent;
    padding: 1rem;
    padding-bottom: 0px;
    color: hsl(var(--aui-foreground));
    outline: 2px solid transparent;
    outline-offset: 2px
}

.aui-edit-composer-footer {
    margin-left: 0.75rem;
    margin-right: 0.75rem;
    margin-bottom: 0.75rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    align-self: flex-end
}

/* assistant message */

.aui-assistant-message-root {
    display: grid;
    grid-template-columns: auto auto 1fr;
    grid-template-rows: auto 1fr;
    position: relative;
    width: 100%;
    max-width: var(--aui-thread-max-width);
    padding-top: 1rem;
    padding-bottom: 1rem
}

:where(.aui-assistant-message-root) > .aui-avatar-root {
    grid-column-start: 1;
    grid-row: 1 / -1;
    grid-row-start: 1;
    margin-right: 1rem
}

:where(.aui-assistant-message-root) > .aui-branch-picker-root {
    grid-column-start: 2;
    grid-row-start: 2;
    margin-left: -0.5rem;
    margin-right: 0.5rem
}

:where(.aui-assistant-message-root) > .aui-assistant-action-bar-root {
    grid-column-start: 3;
    grid-row-start: 2;
    margin-left: -0.25rem
}

:where(.aui-assistant-message-root) > .aui-assistant-message-content {
    grid-column: span 2 / span 2;
    grid-column-start: 2;
    grid-row-start: 1;
    margin-top: 0.375rem;
    margin-bottom: 0.375rem
}

.aui-assistant-message-content {
    max-width: calc(var(--aui-thread-max-width) * 0.8);
    overflow-wrap: break-word;
    line-height: 1.75rem;
    color: hsl(var(--aui-foreground))
}

/* assistant action bar */

.aui-assistant-action-bar-root {
    display: flex;
    gap: 0.25rem;
    color: hsl(var(--aui-muted-foreground))
}

:where(.aui-assistant-action-bar-root)[data-floating] {
    position: absolute;
    border-radius: calc(var(--aui-radius) - 2px);
    border-width: 1px;
    background-color: hsl(var(--aui-background));
    padding: 0.25rem;
    --aui-shadow: 0 1px 2px 0 rgb(0 0 0 / 0.05);
    --aui-shadow-colored: 0 1px 2px 0 var(--aui-shadow-color);
    box-shadow: var(--aui-ring-offset-shadow, 0 0 #0000), var(--aui-ring-shadow, 0 0 #0000), var(--aui-shadow)
}

:where(.aui-assistant-action-bar-feedback-positive)[data-submitted] {
    --aui-text-opacity: 1;
    color: rgb(22 163 74 / var(--aui-text-opacity, 1))
}

:where(.aui-assistant-action-bar-feedback-positive)[data-submitted]:hover {
    --aui-text-opacity: 1;
    color: rgb(21 128 61 / var(--aui-text-opacity, 1))
}

@media (prefers-color-scheme: dark) {
    :where(.aui-assistant-action-bar-feedback-positive)[data-submitted] {
        --aui-text-opacity: 1;
        color: rgb(34 197 94 / var(--aui-text-opacity, 1))
    }
    :where(.aui-assistant-action-bar-feedback-positive)[data-submitted]:hover {
        --aui-text-opacity: 1;
        color: rgb(74 222 128 / var(--aui-text-opacity, 1))
    }
}

:where(.aui-assistant-action-bar-feedback-negative)[data-submitted] {
    --aui-text-opacity: 1;
    color: rgb(220 38 38 / var(--aui-text-opacity, 1))
}

:where(.aui-assistant-action-bar-feedback-negative)[data-submitted]:hover {
    --aui-text-opacity: 1;
    color: rgb(185 28 28 / var(--aui-text-opacity, 1))
}

@media (prefers-color-scheme: dark) {
    :where(.aui-assistant-action-bar-feedback-negative)[data-submitted] {
        --aui-text-opacity: 1;
        color: rgb(239 68 68 / var(--aui-text-opacity, 1))
    }
    :where(.aui-assistant-action-bar-feedback-negative)[data-submitted]:hover {
        --aui-text-opacity: 1;
        color: rgb(248 113 113 / var(--aui-text-opacity, 1))
    }
}

/* branch picker */

.aui-branch-picker-root {
    display: inline-flex;
    align-items: center;
    font-size: 0.75rem;
    line-height: 1rem;
    color: hsl(var(--aui-muted-foreground))
}

.aui-branch-picker-state {
    font-weight: 500
}

/* text */

.aui-text {
    white-space: pre-line
}

@keyframes aui-pulse {
    50% {
        opacity: .5
    }
}

.aui-text-running::after {
    animation: aui-pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
    font-family: ui-sans-serif, system-ui, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    --aui-content: '\25CF';
    content: var(--aui-content)
}

.aui-text-running:where([dir="ltr"], [dir="ltr"] *)::after {
    margin-left: 0.25rem
}

.aui-text-running:where([dir="rtl"], [dir="rtl"] *)::after {
    margin-right: 0.25rem
}

/* thread-list */

.aui-thread-list-root {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    gap: 0.375rem
}

.aui-thread-list-item {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    border-radius: var(--aui-radius);
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms
}

.aui-thread-list-item:hover {
    background-color: hsl(var(--aui-muted))
}

.aui-thread-list-item:focus-visible {
    background-color: hsl(var(--aui-muted));
    outline: 2px solid transparent;
    outline-offset: 2px;
    --aui-ring-offset-shadow: var(--aui-ring-inset) 0 0 0 var(--aui-ring-offset-width) var(--aui-ring-offset-color);
    --aui-ring-shadow: var(--aui-ring-inset) 0 0 0 calc(2px + var(--aui-ring-offset-width)) var(--aui-ring-color);
    box-shadow: var(--aui-ring-offset-shadow), var(--aui-ring-shadow), var(--aui-shadow, 0 0 #0000);
    --aui-ring-color: hsl(var(--aui-ring))
}

.aui-thread-list-item[data-active] {
    background-color: hsl(var(--aui-muted))
}

.aui-thread-list-new {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 0.25rem;
    border-radius: var(--aui-radius);
    padding-left: 0.625rem;
    padding-right: 0.625rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    text-align: start
}

.aui-thread-list-new:hover {
    background-color: hsl(var(--aui-muted))
}

.aui-thread-list-new[data-active] {
    background-color: hsl(var(--aui-muted))
}

.aui-thread-list-new > .lucide-plus {
    width: 1.25rem;
    height: 1.25rem
}

.aui-thread-list-item-trigger {
    flex-grow: 1;
    padding-left: 0.75rem;
    padding-right: 0.75rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    text-align: start
}

.aui-thread-list-item-title {
    font-size: 0.875rem;
    line-height: 1.25rem
}

.aui-thread-list-item-archive {
    margin-left: auto;
    margin-right: 0.75rem;
    width: 1rem;
    height: 1rem;
    padding: 0px;
    color: hsl(var(--aui-foreground))
}

.aui-thread-list-item-archive:hover {
    color: hsl(var(--aui-primary))
}
